import { Component, OnInit } from '@angular/core';
import {Observable} from 'rxjs';
import { interval } from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  reverse = false;
  counter = 0;
  imageArray = ['cover_1', 'cover_2', 'cover_3', 'cover_4', 'cover_5', 'cover_6'];
  imagePath = "../../assets/images/" + this.imageArray[0] + ".jpg";
  constructor() {
  }

  ngOnInit() {
    this.getImage();
    // this.imagePath = 'url(../../assets/images/' + this.imageArray[0] + '.jpg) 0 0';
  }

  addReverseClass() {
    this.reverse = true;
  }

  getImage() {
    interval(5000).subscribe((val) => {
      console.log(this.imageArray.length, 'this.imageArray.length');
      if (this.counter == this.imageArray.length) {
        this.counter = 0;
        //   index = 0;
      }
      this.imagePath = "../../assets/images/" + this.imageArray[this.counter] + ".jpg";
      this.counter++;
      // index = index + 1;
      // console.log(index);
    });
  }

}
